import { IDataType } from "../abstractcomponent";
import { IComponentSerializer } from "../icomponentserializer";
import { isNullOrUndefined } from "util";

/**
 * 
 */
export class BasicType implements IDataType,IComponentSerializer{

   /** */
    ParamType:string;

    /** */
    getDescription(): string {
        if(this.ParamType==''||isNullOrUndefined(this.ParamType)){
            return '';
        }
        return this.ParamType;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj=new Object();
        obj["ParamType"]=this.ParamType;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.ParamType=obj["ParamType"];
    }
}

/**
 * 
 */
export class ComplexType implements IDataType,IComponentSerializer{
   
    /**Id */
    ModelId:string;

    /** */
    ModelName:string;

     /** */
    getDescription(): string {
        if(this.ModelName==''||isNullOrUndefined(this.ModelName)){
            return '';
        }
        return this.ModelName;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj=new Object();
        obj["ModelId"]=this.ModelId;
        obj["ModelName"]=this.ModelName;
        return obj;
    }
    
     /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.ModelId=obj["ModelId"];
        this.ModelName=obj["ModelName"];
    }
}

/**
 * 
 */
export class DictionaryType implements IDataType,IComponentSerializer {
    
    /**Key */
    IsTkeyBasic:boolean;

    /**Key */
    Tkey:string;

    /**KeyId */
    TkeyModelId:string;

     /**Value */
    IsTValueBasic:boolean;

    /**Value */
    TValue:string;

     /**ValueId */
    TValueModelId:string;

     /** */
    getDescription(): string {
        if(this.Tkey==''||isNullOrUndefined(this.Tkey)){
            return '';
        }
        if(this.TValue==''||isNullOrUndefined(this.TValue)){
            return '';
        }
        return `Dictionary<${this.Tkey},${this.TValue}>`;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj=new Object();
        obj["IsTkeyBasic"]=this.IsTkeyBasic;
        obj["Tkey"]=this.Tkey;
        obj["TkeyModelId"]=this.TkeyModelId;
        obj["IsTValueBasic"]=this.IsTValueBasic;
        obj["TValue"]=this.TValue;
        obj["TValueModelId"]=this.TValueModelId;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.IsTkeyBasic = obj["IsTkeyBasic"] as boolean;
        this.Tkey = obj["Tkey"];
        this.TkeyModelId = obj["TkeyModelId"];
        this.IsTValueBasic = obj["IsTValueBasic"] as boolean;
        this.TValue = obj["TValue"];
        this.TValueModelId = obj["TValueModelId"];
    }
}

/**
 * 
 */
export class ListType implements IDataType,IComponentSerializer{

    /** */
    IsBasic:boolean;

    /**Id */
    ModelId:string;

    /** */
    ModelName:string;

    /** */
    getDescription(): string {
        if(this.ModelName==''||isNullOrUndefined(this.ModelName)){
            return '';
        }
        return  `List<${this.ModelName}>`;;
    }

     /**
     * 
     */
    serialize(): Object {
        let obj=new Object();
        obj["IsBasic"]=this.IsBasic;
        obj["ModelId"]=this.ModelId;
        obj["ModelName"]=this.ModelName;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.IsBasic = obj["IsBasic"] as boolean;
        this.ModelId = obj["ModelId"];
        this.ModelName = obj["ModelName"];
    }
}

/**
 * 
 */
export class UnSupportType implements IDataType,IComponentSerializer{
   
    /** */
    ParamType:string;

     /** */
    getDescription(): string {
        if(this.ParamType==''||isNullOrUndefined(this.ParamType)){
            return '';
        }
        return this.ParamType;
    }

    /**
     * 
     */
    serialize(): Object {
        let obj=new Object();
        obj["ParamType"]=this.ParamType;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.ParamType = obj["ParamType"];
    }
}


