import { BasicType, ComplexType, ListType, DictionaryType, UnSupportType } from "./datatype/datatype";
import { IComponentSerializer } from "./icomponentserializer";

/**
 * 
 */
export abstract class AbstractComponent{
    /**Id */
    Id:string;

    /** */
    Code:string;

     /** */
    Name:string;

     /** */
    AssemblyName:string;

     /** */
    FullClassName:string;

     /** */
    Description:string;

     /** */
    Models:Array<CommonModelRefer>;

     /** */
    Methods:Array<AbstractComponentMethod>;

    /** */
    public abstract getEmptyMethodInstance():AbstractComponentMethod;

     /** */
    public serialize():Object{
        let obj=new Object();
        obj["Id"]=this.Id;
        obj["Code"]=this.Code;
        obj["Name"]=this.Name;
        obj["AssemblyName"]=this.AssemblyName;
        obj["FullClassName"]=this.FullClassName;
        obj["Description"]=this.Description;
        let modelArray =new Array<Object>();
        if(this.Models!=null&&this.Models.length>0){
            this.Models.forEach(model => {
                modelArray.push(model.serialize());
            });
        }
        obj["Models"]=modelArray;
        let methodArray=new Array<Object>();
        if(this.Methods!=null&&this.Methods.length>0){
            this.Methods.forEach(method => {
                methodArray.push(method.serialize());
            });
        }
        obj["Methods"]=methodArray;
        return obj;
    }

    /** */
    public init(obj:Object){
        this.Id=obj["Id"];
        this.Code=obj["Code"];
        this.Name=obj["Name"];
        this.AssemblyName=obj["AssemblyName"];
        this.FullClassName=obj["FullClassName"];
        this.Description=obj["Description"];
        let modelArray=new Array<CommonModelRefer>();
        if(obj["Models"]!=null){
            obj["Models"].forEach(modelJobj => {
                let commonModelRefer=new CommonModelRefer();
                commonModelRefer.init(modelJobj);
                modelArray.push(commonModelRefer);
            });
        }
        this.Models=modelArray;
        let methodArray=new Array<AbstractComponentMethod>();
        if(obj["Methods"]!=null){
            obj["Methods"].forEach(methodJobj => {
                let method=this.getEmptyMethodInstance();
                method.init(methodJobj);
                methodArray.push(method);
            });
        }
        this.Methods=methodArray;
    }
}

/**
 * 
 */
export abstract class AbstractComponentMethod implements IComponentSerializer{

    /**ID */
    Id:string;

     /** */
    Code:string;

     /** */
    Name:string;

     /** */
    Description:string;

     /** */
    Parameters:AbstractComponentParameter[]

    /** */
    public abstract getEmptyParamterInstance():AbstractComponentParameter;

     /** */
    public serialize(): Object {
        let obj=new Object();
        obj["Id"]=this.Id;
        obj["Code"]=this.Code;
        obj["Name"]=this.Name;
        obj["Description"]=this.Description;
        let paramArray=new Array<Object>();
        if(this.Parameters!=null&&this.Parameters.length>0){
            this.Parameters.forEach(param => {
                paramArray.push(param.serialize());
            });
        }
        obj["Parameters"]=paramArray;
        return obj;
    }

     /** */
    public init(obj: Object) {
        this.Id=obj["Id"];
        this.Code=obj["Code"];
        this.Name=obj["Name"];
        this.Description=obj["Description"];
        let parameterArray=new Array<AbstractComponentParameter>();
        if(obj["Parameters"]!=null){
            obj["Parameters"].forEach(paramJobj => {
                let param=this.getEmptyParamterInstance();
                param.init(paramJobj);
                parameterArray.push(param);
            });
        }
        this.Parameters=parameterArray;
    }
}

/**
 * 
 */
export abstract class AbstractComponentParameter implements IComponentSerializer{
    /**Id */
    Id:string;

    /** */
    Code:string;

     /** */
    Name:string;

     /** */
    Classification:Classification;

     /** */
    DataType:IDataType;

     /** */
    IsReturn:boolean;

     /** */
    IsRequired:boolean;

     /** */
    Description:string;
    
    /**
     * 
     */
    public serialize(): Object {
        let obj=new Object();
        obj["Id"]=this.Id;
        obj["Code"]=this.Code;
        obj["Name"]=this.Name;
        obj["Description"]=this.Description;
        obj["Classification"]=this.Classification as number;
        obj["IsReturn"]=this.IsReturn;
        obj["IsRequired"]=this.IsRequired;
        obj["DataType"]=this.DataType;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    public init(obj: Object) {
        this.Id=obj["Id"];
        this.Code=obj["Code"];
        this.Name=obj["Name"];
        this.Classification=obj["Classification"] as number;
        this.IsRequired=obj["IsRequired"] as boolean;
        this.IsReturn=obj["IsReturn"] as boolean;
        this.Description=obj["Description"];
        let dataType:any=null;
        if(this.Classification==Classification.Basic){
            dataType=new BasicType();
            dataType.init(obj["DataType"]);
        }
        else if(this.Classification==Classification.Complex){
            dataType=new ComplexType();
            dataType.init(obj["DataType"]);
        }
        else if(this.Classification==Classification.List){
            dataType=new ListType();
            dataType.init(obj["DataType"]);
        }
        else if(this.Classification==Classification.Dictionary){
            dataType=new DictionaryType();
            dataType.init(obj["DataType"]);
        }
        else if(this.Classification==Classification.UnSupport){
            dataType=new UnSupportType();
            dataType.init(obj["DataType"]);
        }
        this.DataType=dataType;
    }
}

/**
 * 
 */
export interface IDataType{
    getDescription():string;
}

/**
 * 
 */
export enum Classification{

    /** */
    Basic=0,

    /** */
    List=1,

    /** */
    Dictionary=2,

    /** */
    Complex=3,

    /** */
    UnSupport=4
}

/**
 * 
 */
export class CommonModelRefer implements IComponentSerializer{

    /**Id */
    ModelId:string;

    /** */
    ModelCode:string;

    /** */
    ModelName:string;

    /** */
    ProjectPath:string;

    /**
     * 
     */
    serialize(): Object {
        let obj=new Object();
        obj["ModelId"]=this.ModelId;
        obj["ModelCode"]=this.ModelCode;
        obj["ModelName"]=this.ModelName;
        obj["ProjectPath"]=this.ProjectPath;
        return obj;
    }

    /**
     * 
     * @param obj Json
     */
    init(obj: Object) {
        this.ModelId=obj["ModelId"];
        this.ModelCode=obj["ModelCode"];
        this.ModelName=obj["ModelName"];
        this.ProjectPath=obj["ProjectPath"];
    }
}




